.backdrop {
  position: fixed;
  overflow: auto;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  transition: opacity 250ms linear, visibility 250ms linear;

  background-size: cover;
  background-position: center;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  & .modal-window {
    transform: translate(-50%, -50%) scale(0);
  }
}

.modal-window {
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 48px;
  padding-bottom: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  border-radius: 15px;
  width: 806px;
  min-height: 558px;
  background-color: getColor('white');
  transition: transform 250ms linear;

  &-close {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 30px;
    height: 30px;
    background-color: inherit;
    cursor: pointer;
  }
  &-dark {
    background-color: black;
  }
}

.icon-close {
  width: 30px;
  height: 30px;
  fill: getColor('primary-text-color');
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: fill 250ms linear;
  .modal-window-close:hover &,
  .modal-window-close:focus & {
    fill: getColor('secondary-text-color');
  }
  &-dark {
    fill: getColor('white');
  }
}

.modal-wrap {
  display: flex;
  flex-wrap: nowrap;
  column-gap: 16px;
}

.modal-img-wrap {
  position: relative;
  margin-bottom: 20px;
  width: 50%;
  height: auto;
}

.modal-wrap-img-btn {
  position: relative;
}

.modal-wrap-img-btn img {
  width: 100%;
  height: auto;
}

.modal-image {
  display: block;
  width: 375px;
  height: 478px;
  border-radius: 5px;
}

.modal-btn-trailer {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #ff6b01;
  transition: color 250ms linear;

  &:hover,
  &:focus {
    color: getColor('white');
  }
}

.btn-trailer-icon {
  width: 25px;
  height: 25px;
  fill: getColor('primary-text-color');
  position: absolute;
  top: 50%;
  left: 55%;
  transform: translate(-50%, -55%);
  transition: fill 250ms linear;

  .modal-btn-trailer:hover &,
  .modal-btn-trailer:focus & {
    fill: getColor('white');
  }
}

.modal-info {
  width: 391px;
}

.modal-info-name {
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.17;
}

.modal-info-list {
  position: relative;
  margin-bottom: 20px;
}

.modal-info-item {
  display: flex;
  flex-grow: 1;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: #8c8c8c;

  &:not(:last-child) {
    margin-bottom: 8px;
  }
}

.modal-info-name-value {
  width: 150px;
}

.modal-info-value {
  width: calc(100% - 150px);
  color: getColor('primary-text-color');

  &-dark {
    color: getColor('white');
  }
}

.modal-info-value-vote,
.modal-info-value-votes {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: 3px;
  padding-left: 10px;
  padding-right: 9px;
  height: 16px;
  background-color: #ff6b01;
  border-radius: 5px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  text-align: center;
  color: getColor('white');
}

.modal-info-value-votes {
  margin-left: 3px;
  margin-right: 0px;
  background-color: getColor('footer-background');
  color: getColor('primary-text-color');
}

.modal-info-pre-about {
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-transform: uppercase;
}

.modal-info-about {
  padding-right: 20px;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
  height: 200px;
  overflow-y: scroll;
}

.btn-modal-wrap {
  display: flex;
  flex-wrap: nowrap;
  justify-content: normal;
  gap: 20px;
}

.modal-btn-wrap {
  position: relative;
}

.modal-btn {
  cursor: pointer;
  width: 136px;
  height: 44px;
  padding: 6px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-align: center;
  text-transform: uppercase;
  color: getColor('white');
  border: 1px solid getColor('secondary-text-color');
  background-color: getColor('secondary-text-color');
  border-radius: 20px;
  transition: background-color 250ms linear, color 250ms linear;

  &:hover,
  &:focus {
    background-color: #e5b99a;
    border: 1px solid #ff6b01;
    color: #ffffff;
  }

  &-watched {
    padding: 6px 6px;
  }

  &-queue {
    padding: 6px;
  }

  &-dark {
    border: 1px solid #ff6b01;
    color: getColor('white');
  }
}

.modal-btn-rem {
  position: absolute;
  top: 0;
  left: 0;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}
