}

html, body {
  scroll-behavior: smooth;
}

html ::-webkit-scrollbar {
  width: 20px;
}

html ::-webkit-scrollbar-track {
  border-radius: 10px;
  box-shadow: inset 0 0 5px gray;
}

html ::-webkit-scrollbar-thumb {
  background: #ff6b01;
  border-radius: 10px;
}

html ::-webkit-scrollbar-thumb:hover {
  background: #ff6b01;
}

body {
  color: getColor("primary-text-color");
  background-color: getColor("white");
  letter-spacing: .03em;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-style: normal;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

ul {
  font-style: normal;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  margin-top: 0;
  margin-bottom: 0;
  display: block;
}

button {
  border: 0;
  padding: 0;
  font-size: 100%;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 1280px) {
  .container {
    width: 100%;
    padding-left: 16px;
    padding-right: 16px;
