}

.header {
  max-width: 100%;
  height: 320px;
  opacity: .9;
  background-image: url("abstract-background.05b8a1f3.png");
  background-repeat: no-repeat;
  background-size: 100% 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 40px 32px;
}

.main-nav {
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo__svg {
  stroke-width: 2px;
  fill: rgba(0, 0, 0, 0);
