  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  position: relative;
}

.main-nav__link:hover {
  cursor: pointer;
  color: #ff6b08;
}

.main-nav__link--current:after {
  content: "";
  width: 100%;
  height: 3px;
  opacity: 1;
  background-color: #ff001b;
  display: block;
  position: absolute;
  bottom: -3px;
}

.segment {
  justify-content: center;
  align-items: center;
  margin-top: 41px;
  display: flex;
}

.search-form {
