}

.form__field {
  width: 100%;
  color: #fff;
  background: none;
  border: 0;
  border-bottom: 2px solid;
  outline: 0;
  padding: 7px 0;
  font-family: inherit;
  font-size: 14px;
  transition: border-color .2s;
}

.form__field::-ms-input-placeholder {
