}

.form__field:placeholder-shown ~ .form__label {
  cursor: text;
  font-size: 14px;
  top: 20px;
}

.form__label {
  font-size: 14px;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 0;
}

.form__field:focus {
  border-width: 3px;
  border-image: linear-gradient(to right, #ff6b08, #ff6b08) 1;
  padding-bottom: 6px;
  font-weight: 700;
}

